import './style/scss/MainApp.scss';

import {React, useState} from 'react';

// Components
import Messages from './Components/Messages';
import UserPage from './Components/UserPage';
import Program from './Components/Program';
import UserSemestersInfo from './Components/UserSemestersInfo';

// Font Awesome
import {
    msgIco, userIco, fileIco, fileInvoiceIco, tableIco, 
    fileSignatureIco, idCardIco, fileContractIco, fileInvoiceDollarIco,
    bookIco, envelopeOpenTextIco, singOutIco
} from './Components/FontAwesome';

const MainApp = ({user}) => {
    const [active, setActive] = useState(() => <UserPage user={user}/>);
    const [lastActivePageId, setLastActivePageId] = useState("userPageId2");

    const handleChangeActivePage = (event) => {
        const activePageId = event.currentTarget.id;

        document.getElementById(activePageId).className = "mainApp-inner-nav-wrapper-item active";
        document.getElementById(lastActivePageId).className = "mainApp-inner-nav-wrapper-item inActive";
        setLastActivePageId(activePageId);

        const activePageIdLast = Number(event.currentTarget.id.slice(-1));
        switch (activePageIdLast) {
            case 1:
                setActive(() => <Messages/>);
                break;
            case 2:
                setActive(() => <UserPage user={user}/>);
                break;
            case 3:
                setActive(() => <UserSemestersInfo user={user}/>)
                break;
            case 4:
                setActive(() => <Program user={user}/>)
                break;
            default:
                break;
        }
    }
        
    return(
        <div className="mainApp mainApp-outer">
            <div className="mainApp-inner">
                <nav className="mainApp-inner-nav">
                    <ul className="mainApp-inner-nav-wrapper">
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="messagesPageId1"
                        >   
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {msgIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                შეტყობინებები
                            </div>
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item active"
                            onClick={handleChangeActivePage}
                            id="userPageId2"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {userIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                პირადი მონაცემები
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userSemestersInfoId3"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {fileIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                სასწავლო ბარათი
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userCoursePlanId4"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {fileInvoiceIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                საგ. პროგრამა
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userRegistrationId5"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {fileSignatureIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                აკადემიური რეგისტრაცია  
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userSemesterPlanId6"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {tableIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                სასწავლო ცხრილი
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userMarksPageGeId7"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {idCardIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                ნიშნების ფურცელი
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userMarksPageEngId8"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {idCardIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                ნიშნების ფურცელი(ENG)
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userExercisesId9"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {fileContractIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                             დავალებები
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userFinancesId10"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {fileInvoiceDollarIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                ფინანსები
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userLibraryId11"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {bookIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                ბიბლიოთეკა
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userStatementsId12"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {envelopeOpenTextIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                განცხადებები
                            </div>
                            
                        </li>
                        <li className="mainApp-inner-nav-wrapper-item"
                            onClick={handleChangeActivePage}
                            id="userExitId13"
                        >
                            <div className="mainApp-inner-nav-wrapper-item-ico">
                                {singOutIco}
                            </div>
                            <div className="mainApp-inner-nav-wrapper-item-text">
                                გასვლა
                            </div>
                            
                        </li>
                    </ul>
                </nav>
                <div className="mainApp-inner-activePage">
                    {active}
                </div>
            </div>
        </div>
    )

}

export default MainApp;
