import {React, useState} from 'react';
import '../style/scss/ComponentsStyles/UserProgram.scss';

const Program = ({user}) => {
    const [schoolIsOpen, setSchoolIsOpen] = useState(false);
    
    const handleSlideDownSchool = () => {
        let element = document.getElementById('schoolInfoSection');
        if (schoolIsOpen) {
            setSchoolIsOpen(false);

            for (let i = 40; i >= 0; i--) {
                setTimeout(() => {
                    console.log(i);
                    element.style.height = `${i}px`;
                }, 500)
            }
        } else {
            setSchoolIsOpen(true);

            for (let i = 1; i <= 40; i++) {
                setTimeout(() => {
                    console.log(i);
                    element.style.height = `${i}px`;
                }, 500)
            }
        }
    }

    return(
        <div className="mainApp-inner-activePage-userProgram">
            <div className="main main-outer">
                <div className="main-inner">
                    <div className="main-inner-school" onClick={handleSlideDownSchool}>
                        <span className="main-inner-school-id" >
                            3
                        </span>
                        <span className="main-inner-school-title">
                            {user.userPage.school}
                        </span>
                    </div>

                    <div id="schoolInfoSection" className="main-inner-schoolInfo">
                        <div className="main-inner-schoolInfo-id">
                            130
                        </div>

                        <div className="main-inner-schoolInfo-title">
                        ელექტრო და კომპიუტერული ინჟინერია, ავტომატიზირებული მართვის სისტემები
                        </div>

                        <div className="main-inner-schoolInfo-creditsTotal">
                            Credits Total: 136
                        </div>

                        <div className="main-inner-schoolInfo-creditsWithoutElective">
                            Credits Without Elective Courses: 133
                        </div>

                        <div className="main-inner-schoolInfo-credit">
                            Credit: 247
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Program;