import React from 'react';

import {sendIco, fileUploadIco} from './FontAwesome';

const Messages = () => {
    return(
        <div className="mainApp-inner-activePage-messages">
            <div className="messages messages-outer">
                <div className="messages-inner">
                        <div className="messages-inner-left">
                            <ul className="messages-inner-left-top">
                                <li className="messages-inner-left-top-student">
                                    სტუდენტი
                                </li>

                                <li className="messages-inner-left-top-professor">
                                    პროფესორი
                                </li>

                                <li className="messages-inner-left-top-administration">
                                    ადმინისტრაცია
                                </li>
                            </ul>
                        

                            <div className="messages-inner-left-search">
                                <div className="messages-inner-left-search-inner">
                                    <input type="text" id="msgSearch" className="messages-inner-left-search-inner-input" placeholder="ძებნა..."/>
                                </div>
                            </div>
                        </div>
                    
                        <div className="messages-inner-right">
                            <div className="messages-inner-right-newMsg">
                                <button className="messages-inner-right-newMsg-btn">
                                    + ახალი შეტყობინება
                                </button>
                            </div>
                            
                            <div className="messages-inner-right-chat">
                                <div className="messages-inner-right-chat-inner">
                                    <div className="messages-inner-right-chat-inner-field">
                                        შეტყობინება არ არის
                                    </div>
                                    
                                    <div className="messages-inner-right-chat-inner-msgArea">
                                        <div className="messages-inner-right-chat-inner-msgArea-textArea">
                                            <textarea placeholder="შეტყობინება..." id="msgTextAreaId"></textarea>
                                        </div>
                                        <div className="messages-inner-right-chat-inner-msgArea-footer">
                                            <div className="messages-inner-right-chat-inner-msgArea-footer-uploadFile">
                                                <label className="messages-inner-right-chat-inner-msgArea-footer-uploadFile-label">
                                                    <input type="file" style={{display: 'none'}} id="msgFileUpload"/>
                                                    <span id="fileUploadIcoId">{fileUploadIco}</span>
                                                    ფაილის მიმაგრება
                                                </label>
                                            </div>
                                            <div className="messages-inner-right-chat-inner-msgArea-footer-send">
                                                <button className="messages-inner-right-chat-inner-msgArea-footer-send-btn">
                                                    <span id="sendIcoId">{sendIco}</span>
                                                    გაგზავნა
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>  
                    </div>               
                </div>
            </div>
    );
};

export default Messages;
