import '../style/scss/ComponentsStyles/UserSemestersInfo.scss';

import {React} from 'react';
import agruniSubjects from '../Subjects/agruniSubjects';

import {chartBarIco} from './FontAwesome';

const markToAF = (mark) => {
    if (mark < 41) {
        return 'F'
    } else if (mark >=41 && mark < 51) {
        return 'Fx'
    } else if (mark >=51 && mark < 61) {
        return 'E'
    } else if (mark >=61 && mark < 71) {
        return 'D'
    } else if (mark >=71 && mark < 81) {
        return 'C'
    } else if (mark >=81 && mark < 91) {
        return 'B'
    } else if (mark >=91) {
        return 'A'
    }
};

const MarkModal = () => {
    return (
      <>
        
      </>
    );
}

const UserSemester = ({semesterInfo, userId, semester}) => {
    const userSubjects = semesterInfo.userSubjects;
    const userSubjectsIds = userSubjects.map(elem => elem.id);
    const userSubjectsMarks = userSubjects.map(elem => elem.mark);

    let row = userSubjectsIds.map((elem, ctn) => {
        const userMark = userSubjectsMarks[ctn];
        let userMarkClass = "semestersInfo-inner-gridMain-item";
        if (userMark >= 51) {
            userMarkClass = `${userMarkClass} userMarkPassed`;
        } else if (userMark >= 41 && userMark < 51) {
            userMarkClass = `${userMarkClass} userMarkFX`;
        } else if (userMark < 41) {
            userMarkClass = `${userMarkClass} userMarkNotPassed`;
        }
            return (
                <div key={`${userId}-${semester + 1}-${elem}`} className="semestersInfo-inner-gridMain">
                    <div className="semestersInfo-inner-gridMain-item">
                        {agruniSubjects[elem].subjName}
                    </div>

                    <div className="semestersInfo-inner-gridMain-item">
                        {agruniSubjects[elem].lecturer}
                    </div>

                    <div className="semestersInfo-inner-gridMain-item">
                        {agruniSubjects[elem].credit}
                    </div>

                    <div className="semestersInfo-inner-gridMain-item">
                        {userMark}
                    </div>

                    <div className={userMarkClass}>
                        {markToAF(userMark)}
                    </div>

                    <div className="semestersInfo-inner-gridMain-item">
                        <MarkModal subj={agruniSubjects[elem].subjName}/>
                    </div>

                    <div className="semestersInfo-inner-gridMain-item">
                        <a rel="noreferrer" href={agruniSubjects[elem].syllabus} target="_blank"> სილაბუსი </a> 
                    </div>

                    <div className="semestersInfo-inner-gridMain-item">
                        <span id="chartBarIcoId">{chartBarIco}</span>
                    </div>
                </div>
            )
        }
    );

    return(
        <div className="semestersInfo semestersInfo-outer">
            <div className="semestersInfo-inner">
                <div className="semestersInfo-inner-top">
                    {semester + 1} სემესტრი
                </div>

                <div className="semestersInfo-inner-gridHead">
                    <div className="semestersInfo-inner-gridHead-item">საგნის დასახელება</div>
                    <div className="semestersInfo-inner-gridHead-item">ლექტორი</div>
                    <div className="semestersInfo-inner-gridHead-item">კრედ.</div>
                    <div className="semestersInfo-inner-gridHead-item">ქულა</div>
                    <div className="semestersInfo-inner-gridHead-item">შეფასება</div>
                    <div className="semestersInfo-inner-gridHead-item">დეტალური</div>
                    <div className="semestersInfo-inner-gridHead-item">სილაბუსი</div>
                    <div className="semestersInfo-inner-gridHead-item">სტატ.</div>      
                </div>

                {row}

            </div>
        </div>
    )
};

const UserSemestersInfo = ({user}) => {
    const semesters = user.userSemestersInfo.userSemesters.map(elem => elem);

    let userSemesters = semesters.map((elem, semester=0) => {
        return <UserSemester 
                key={`${semester}-${user.userId}`} 
                userId = {user.userId} 
                finished={elem.userSemesterFinished} 
                semester={semester} 
                semesterInfo={elem} />
    });

    return(
        <div className="mainApp-inner-activePage-userSemestersInfo">
            {userSemesters}
        </div>
    );
}

export default UserSemestersInfo;